.head-product {
  display: block;
  justify-content: center;
  align-items: center;
}

.head-product-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: 32px;
  margin-right: 32px;
}

.title-busket {
  margin-right: 25px;
  width: 40px;
  height: 40px;
}

.products-page {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 2rem;
  padding: 2rem;
  justify-items: center;
}

